.iconVolumeOff {
  color: rgb(10,10,10);
  color: var(--blackColor);
  cursor: pointer;
  text-shadow: none;
  transition: text-shadow 1s;
  font-size: 2rem;
  outline: none;
}

.iconVolumeOn {
  color: rgb(10,10,10);
  color: var(--blackColor);
  cursor: pointer;
  text-shadow: 0px 0px 0.1rem rgb(50, 200, 255);
  text-shadow: 0px 0px 0.1rem var(--padSecondColor);
  transition: text-shadow 1s;
  font-size: 2rem;
  outline: none;
}

.iconWrapper {
  text-align: right;
}

.divVolumeSlider {
  width: 100%;
  display: flex;
  margin-top: 1rem;
  align-self: center;
}

.inputSliderWrapper {
  margin-right: 0.6rem;
  flex: 1 0 auto;
}

input[type="range"] {
  width: 100%;
  -webkit-appearance: none;
  background: transparent;
  padding-top: 0.65rem; /*To adjust alignment with sound icon*/
}

input[type="range"]:focus {
  outline: none;
}

input[type="range"]::-webkit-slider-runnable-track {
  height: 0.4rem;
  cursor: pointer;
  background: rgb(10,10,10);
  background: var(--blackColor);
  border-radius: 0.5rem;
  border: 0.1rem solid rgb(50,50,50);
  border: 0.1rem solid var(--darkGrayColor);
  box-shadow: 0.12rem 0.12rem 1.2rem black;
  transition: box-shadow 1s;
}

input[type="range"].sliderOn::-webkit-slider-runnable-track {
  box-shadow: 0.12rem 0.12rem 1.2rem black, 0px 0px 0.2rem 0.02rem rgb(50, 200, 255);
  box-shadow: 0.12rem 0.12rem 1.2rem black, 0px 0px 0.2rem 0.02rem var(--padSecondColor);
  transition: box-shadow 1s;
}

input[type="range"]::-webkit-slider-thumb {
  border: 0.06rem solid rgb(50,50,50);
  border: 0.06rem solid var(--darkGrayColor);
  border-radius: 0.5rem;
  height: 1.4rem;
  width: 0.7rem;
  background: rgb(80,80,80);
  background: var(--lightGrayColor);
  cursor: pointer;
  -webkit-appearance: none;
  margin-top: -0.6rem;
  box-shadow: 0.24rem 0.24rem 1.2rem black;
}

input[type="range"]::-moz-range-track {
  height: 0.4rem;
  cursor: pointer;
  box-shadow: 0.12rem 0.12rem 1.2rem black;
  background: #000000;
  border-radius: 0.5rem;
  border: 0.05rem solid rgb(50,50,50);
  border: 0.05rem solid var(--darkGrayColor);
  transition: box-shadow 1s;
}

input[type="range"].sliderOn::-moz-range-track {
  box-shadow: 0.12rem 0.12rem 1.2rem black, 0px 0px 0.2rem 0.02rem rgb(50, 200, 255);
  box-shadow: 0.12rem 0.12rem 1.2rem black, 0px 0px 0.2rem 0.02rem var(--padSecondColor);
  transition: box-shadow 1s;
}

input[type="range"]::-moz-range-thumb {
  border: 0.05rem solid rgb(50,50,50);
  border: 0.05rem solid var(--darkGrayColor);
  border-radius: 0.5rem;
  background: #ffffac;
  cursor: pointer;
  height: 1.4rem;
  width: 0.7rem;
  box-shadow: 0.24rem 0.24rem 1.2rem black;
  background-color: rgb(80,80,80);
  background-color: var(--lightGrayColor);
}

input[type="range"]::-ms-track {
  height: 0.4rem;
  cursor: pointer;
  background: transparent;
  border-color: transparent;
  color: transparent;
  box-shadow: 0.12rem 0.12rem 1.2rem black;
  transition: box-shadow 1s;
}

input[type="range"].sliderOn::-ms-track {
  box-shadow: 0.12rem 0.12rem 1.2rem black, 0px 0px 0.2rem 0.02rem rgb(50, 200, 255);
  box-shadow: 0.12rem 0.12rem 1.2rem black, 0px 0px 0.2rem 0.02rem var(--padSecondColor);
  transition: box-shadow 1s;
}

input[type="range"]::-ms-fill-lower {
  background: #000000;
  border: 0.05rem solid rgb(50,50,50);
  border: 0.05rem solid var(--darkGrayColor);
  border-radius: 0.5rem;
  box-shadow: 0.12rem 0.12rem 1.2rem black;
}

input[type="range"]::-ms-fill-upper {
  background: #000000;
  border: 0.05rem solid rgb(50,50,50);
  border: 0.05rem solid var(--darkGrayColor);
  border-radius: 0.5rem;
  box-shadow: 0.12rem 0.12rem 1.2rem black;
}

input[type="range"]::-ms-thumb {
  border: 0.1rem solid #000000;
  border-radius: 0.5rem;
  height: 1.4rem;
  width: 0.7rem;
  background: #ffffac;
  cursor: pointer;
  box-shadow: 0.24rem 0.24rem 1.2rem black;
}

input[type="range"]:focus::-ms-fill-lower {
  background: #000000;
}

input[type="range"]:focus::-ms-fill-upper {
  background: #000000;
}
